.btn {
  line-height: 1.5;
  display: inline-block;
  font-weight: 400;
  text-align: center;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  border: 1px solid transparent;
  white-space: nowrap;
  padding: 0 15px;
  font-size: 14px;
  border-radius: 4px;
  height: 32px;
  user-select: none;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  position: relative;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.015);
  color: #d46b08;
  background-color: #fff;
  border-color: #d9d9d9;
  outline: none;
}

.btn-primary {
  color: #fff;
  background-color: #00375B;
  border-color: #00375B;
  box-shadow: 0 2px 0 rgba(0, 0, 0, 0.035);
}

.color-wrapper {
  padding: 0 20px 30px;
  border: 1px solid #ccc;
  float: left;
}

.color-wrapper .title {
  padding: 15px 0;
  border-bottom: 1px solid #ccc;
  margin-bottom: 15px;
}

.wrapper-body > .item {
  margin-bottom: 5px;
}

.wrapper-body > .item > div {
  display: inline-block;
  vertical-align: top;
}

.wrapper-body > .item > :first-child {
  width: 150px;
  line-height: 24px;
}

.container {
  overflow: hidden;
  padding: 60px 20px 20px 30px;
}

.container > div {
  margin-bottom: 15px;
}